@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');
/****/

.body {
    flex-direction: column;
    display: flex;
    align-items: center;
}

#sOne, #sTwo, #sThree, #sFour {
    height: 60px;
}

.body h2 {
    color: #FFD166;
    font-weight: 200;
    font-size: 1.8em;
    font-family: 'Oswald', sans-serif;
    margin-bottom: 2em;
}

.info-box {
    border-radius: 8px;
    background-color: #19224b;
    width: 80%;
    height: 100%;
    z-index: 2;
    margin-bottom: 170px;
    box-shadow: -3.5px 3px 0 0 #FF619C;
}

.info-box p {
    font-family: 'Raleway', sans-serif;
    color: white;
    padding: .5em 1.4em;
    line-height: 1.3em;
}

.sig {
    font-family: 'Sacramento', cursive;
    font-size: 1.8em;
    color: #FF619C;
    font-weight: 200;
    margin-bottom: 10px;
}

@media (min-width: 1125px) {
    .info-box {
        width: 60%;
    }
    .info-box p {
        font-size: 1.1em;
    }
    .body h2 {
        font-size: 2em;
    }
    .card-holder {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}

@media (min-width: 1500px) {
    .info-box {
        width: 40%;
    }
}