body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/****/

a {
    text-decoration: none;
}

.icon {
    width: 2em;
}

.icon:hover {
    cursor: pointer;
}

.white-svg {
    -webkit-filter: invert(98%) sepia(2%) saturate(6%) hue-rotate(22deg) brightness(101%) contrast(105%);
            filter: invert(98%) sepia(2%) saturate(6%) hue-rotate(22deg) brightness(101%) contrast(105%);
}

.nav-bg {
    background-color: rgba(25, 34, 75, 1)
}

.icon-container {
    display: flex;
    justify-content: space-between;
    margin: 1em 1em;
    width: 33%;
    max-width: 300px;
}

.link-container {
    display: flex;
    justify-content: space-between;
    width: 48%;
    margin: 1.5em 1em;
    max-width: 700px;
}

.link-container a {
    color: white;
    font-size: 1.1em;
    font-family: "Raleway", sans-serif;
}

.link-container a:hover {
    color: #FF619C;
}

.nav-container {
    display: flex;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    z-index: 3;
    height: 4.8em;
    transition: 200ms linear all;
}

.col-center {
    align-items: center;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.icon-droptext {
    color: white;
    font-size: .8em;
    opacity: 0;
}

.show {
    opacity: 1 !important;
}

.l {
    background-color: white;
    height: 2.3px;
    transition: 300ms ease-in-out all;
}

.one {
    height: 2.5px;
    width: 34px;
}

.one-active {
    height: 2.5px;
    opacity: 0;
    width: 34px;
}

.two {
    height: 2.5px;
    width: 24px;
}

.two-active {
    background-color: #FF619C;
    height: 2.5px;
    -webkit-transform: translateY(-2px) rotate(45deg);
            transform: translateY(-2px) rotate(45deg);
    width: 34px;
}

.three {
    height: 2.2px;
    width: 12px;
}

.three-active {
    background-color: #FF619C;
    height: 2.5px;
    -webkit-transform: translateY(-13px) rotate(-45deg);
            transform: translateY(-13px) rotate(-45deg);
    width: 34px;
}

.hamburg {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 24px;
    justify-content: space-between;
    margin: 1em 1em;
    z-index: 2;
}

.hamburg:hover {
    cursor: pointer;
}

.dropdown {
    background-color: #10113E;
    border-bottom: 1px solid #FFF;
    display: flex;
    flex-direction: column;
    left: 0;
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    right: 0;
    text-align: center;
    top: -280px;
    transition: ease-in-out all 500ms;
    width: 100%;
}

.show-drop {
    top: 0 !important;
}

.drop-text {
    color: white;
    font-family: 'Oswald', sans-serif;
    font-size: 1.2em;
}

.drop-text:hover {
    color: #FF619C;
    cursor: pointer;
}

@media (max-width: 1125px) {
    .link-container {
        display: none;
    }
}

@media (min-width :1125px) {
    .icon {
        width: 2.2em;
    }
    .hamburg {
        display: none;
    }
}
