    z-index: 2;
}

.arrow {
    -webkit-animation: bounce 1s infinite;
            animation: bounce 1s infinite;
    cursor: pointer;
    -webkit-filter: invert(100%) sepia(100%) saturate(11%) hue-rotate(236deg) brightness(105%) contrast(103%);
            filter: invert(100%) sepia(100%) saturate(11%) hue-rotate(236deg) brightness(105%) contrast(103%);
    width: 4em;
    margin-bottom: 10em;
    z-index: 2;
}

@-webkit-keyframes bounce {
    0% {
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px)
    }
    50% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
    100% {
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px)
    }
}

@keyframes bounce {
    0% {
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px)
    }
    50% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
    100% {
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px)
    }
}

