.body {
    flex-direction: column;
    display: flex;
    align-items: center;
}

#sOne, #sTwo, #sThree, #sFour {
    height: 60px;
}

.body h2 {
    color: #FFD166;
    font-weight: 200;
    font-size: 1.8em;
    font-family: 'Oswald', sans-serif;
    margin-bottom: 2em;
}

.info-box {
    border-radius: 8px;
    background-color: #19224b;
    width: 80%;
