    z-index: 2;
    margin-bottom: 170px;
    box-shadow: -3.5px 3px 0 0 #FF619C;
}

.info-box p {
    font-family: 'Raleway', sans-serif;
    color: white;
    padding: .5em 1.4em;
    line-height: 1.3em;
}

.sig {
