.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 8px;
    width: 70%;
    height: 460px;
    background-color: rgb(22, 20, 20);
    margin-bottom: 8em;
    max-width: 483px;
}

.card p {
    color: white;
    font-family: "Raleway", sans-serif;
    font-size: 1em;
    padding: 1em 1.4em;
    overflow-y: auto;
    height: 22%;
}

.card p::-webkit-scrollbar {
    background-color: rgb(22, 20, 20);
}

.card p::-webkit-scrollbar-thumb {
    background-color: rgb(48, 44, 44);
    border-radius: 10%;
}

.dev {
    box-shadow: -3.5px 3px 0 0 #63B5D1
}

.des {
    box-shadow: -3.5px 3px 0 0 #FF619C;
}

.card-img {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    width: 100%;
    height: 50%;
    image-rendering: -moz-crisp-edges;
    image-rendering: L -o-crisp-edges;
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
    -ms-interpolation-mode: nearest-neighbor;
}

.card-title {
    font-family: "Raleway", sans-serif;
    color: white;
    font-size: 1.2em;
    margin-top: 4em;
    letter-spacing: 1.4px;
    margin-bottom: 1.5em;
}

.button-contain {
    width: 80%;
    display: flex;
    justify-content: space-between;
}

.dev button {
    background-color: #19647E;
    border: 2px solid #63B5D1;
}

.dev button:hover {
    background-color: #5195ac;
}

.des button {
    background-color: #ac2858;
    border: 2px solid #FF619C;
}

.des button:hover {
    background-color: #bd6384;
}

.dev button, .des button {
    border-radius: 2px;
    color: white;
    font-family: "Oswald", sans-serif;
    font-size: 1.2em;
    margin-top: .4em;
    padding: 2px 16px;
    transition: 200ms ease-in-out all;
}

button:hover {
    cursor: pointer;
}

.card-holder {
    display: flex;
    flex-direction: column;
}

@media (min-width :1125px) {
    .card-holder {
        flex-direction: row;
    }
}