h2 {
    color: #FFD166;
    font-weight: 200;
    font-size: 1.8em;
    font-family: 'Oswald', sans-serif;
    margin-bottom: 1em;
}

.reg-flex {
    display: flex;
    justify-content: space-between;
}

#name, #phone {
    width: 80%;
    height: 2em;
}

#email {
    height: 2em;
}

#email-l, #msg-l {
    margin: .4em auto;
}

.contact-box {
    display: flex;
    flex-direction: column;
    margin-bottom: 8em;
}

.contact-box label {
    font-family: "Raleway", sans-serif;
    color: white;
    font-size: 1.2em;
    margin-bottom: .5em;
}

.contact-box input, textarea {
    font-family: "Raleway", sans-serif;
    color: white;
    margin-bottom: 1em;
    border-radius: 4px;
    background-color: #000;
    border: none;
    box-shadow: -2px 2px 0 0 #63B5D1;
    padding-left: .3em;
}

.contact-box button {
    width: 30%;
    margin: .8em auto;
    background-color: #19647E;
    border: 2px solid #63B5D1;
    border-radius: 2px;
    color: white;
    font-family: "Oswald", sans-serif;
    font-size: 1.2em;
    transition: 200ms ease-in-out all;
}

.contact-box button:hover {
    background-color: #5195ac;
}

.contact-box h3 {
    color: #FFD166;
    font-weight: 200;
    font-size: 1.4em;
    font-family: 'Oswald', sans-serif;
    margin: .2em auto;
}

textarea {
    resize: none;
}

.contact-box input:focus, textarea:focus {
    outline: none;
}