h1 {
    color: #FFD166;
    font-weight: 200;
    font-size: 2em;
    font-family: 'Oswald', sans-serif;
    margin-top: 4em;
}

h3 {
    color: pink;
}

.house {
    margin: 1em;
}

.house:hover {
    filter: invert(60%) sepia(35%) saturate(4365%) hue-rotate(304deg) brightness(101%) contrast(101%);
}

.des-img {
    width: 80%;
    margin: 1em 0;
}

.info-text {
    width: 60%;
    color: white;
    border-radius: 8px;
    padding: 1.5em 2.2em;
    background-color: #19224b;
    box-shadow: -3.5px 3px 0 0 #FF619C;
    margin: 4em 0;
}

.icon-droptext {
    color: white;
    font-size: .8em;
    opacity: 0;
}

.show {
    opacity: 1 !important;
}

.icon {
    width: 2em;
}

.icon:hover {
    cursor: pointer;
}

.text-link {
    color: cyan;
    font-weight: 700;
}

.text-link:visited {
    color: cyan;
}